@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300;400&family=Open+Sans:wght@300;400&family=Oswald:wght@400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300;400&family=Open+Sans:wght@300;400&family=Oswald:wght@400;500&display=swap);
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
      monospace;
  }
.App {
  text-align: center;
}


.App-link {
  color: #61dafb;
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}
/* font-family: 'Nunito', sans-serif;
font-family: 'Open Sans', sans-serif;
font-family: 'Oswald', sans-serif; */

.navbar {
  background-color: #D4ECEF;
  margin-bottom: 1rem;
}

.nav-item {
  font-family: 'Nunito', sans-serif;
  color:#6f00ff ;
}
.landing {
  font-family: 'Nunito', sans-serif;
  /* Animation */
  /*Shrinking for mobile*/
}

.landing .hero {
  padding: 80px 0;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.landing .hero img {
  border-radius: 10px;
}

.landing .hero div{
  width: 90%;
}

.landing .hero div h1 {
  font-family: 'Oswald', sans-serif;
  font-size: 72px;
  color: #9D99E6;
}

.landing .hero div h2 {
  font-size: 64px;
  color: #6580f8;
  margin: 1rem;
  padding-bottom: 1rem;
}

.landing .hero div p {
  font-family: 'Open Sans', sans-serif;
  font-size: 28px;
  color: #3C5487;
}

.landing .hero div button {
  box-shadow: none;
  padding: 10px 30px;
  border: none;
  border-radius: 20px;
  background-image: linear-gradient(to right, #3A31E0, #9D99E6);
  color: white;
  font-size: 24px;
}

.landing .hero .landing1 {
  width: 50%;
  padding: 0 4rem 0 0;
}

.landing .hero .skills {
  width: 30%;
}

.landing .waves {
  position: relative;
  width: 100%;
  height: 18vh;
  margin-bottom: -7px;
  /*Fix for safari gap*/
  min-height: 100px;
  max-height: 150px;
}


@-webkit-keyframes move-forever {
  0% {
    -webkit-transform: translate3d(-90px, 0, 0);
            transform: translate3d(-90px, 0, 0);
  }
  100% {
    -webkit-transform: translate3d(85px, 0, 0);
            transform: translate3d(85px, 0, 0);
  }
}

@keyframes move-forever {
  0% {
    -webkit-transform: translate3d(-90px, 0, 0);
            transform: translate3d(-90px, 0, 0);
  }
  100% {
    -webkit-transform: translate3d(85px, 0, 0);
            transform: translate3d(85px, 0, 0);
  }
}

@media (max-width: 768px) {
  .landing .waves {
    height: 40px;
    min-height: 40px;
  }
  .landing h1 {
    font-size: 24px;
  }
}

.landing .hero2 {
  padding: 80px 0;
}

.landing .hero2 h1 {
  font-family: 'Oswald', sans-serif;
  font-size: 48px;
  color: #76C9BF;
  padding: 2rem;
}

.landing .hero2 p {
  font-family: 'Open Sans', sans-serif;
  font-size: 24px;
  color: #3C5487;
  padding-bottom: 2rem;
}

.landing .hero2 button {
  box-shadow: none;
  padding: 10px 30px;
  border: none;
  border-radius: 20px;
  background-image: linear-gradient(to right, #3A31E0, #9D99E6);
  color: white;
  font-size: 24px;
}

.landing .hero2 .blurbs {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.landing .hero2 .blurbs > * {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.landing .hero2 .blurbs > * > * {
  margin: 0.5rem;
}

.landing .hero2 .blurbs > * h2 {
  font-size: 36px;
  color: #3C5487;
  margin: 1rem;
  padding-bottom: 2rem;
}

.landing .hero2 .blurbs > * img {
  width: 70%;
}
